export default {
  name: "InputItem",
  props: {
    todo: {
      type: Object
    },
    step: {
      type: Number,
      validator: value => value >= 0
    }
  },

  data() {
    return {
      tempTodo: { ...this.todo
      },
      partnerList: [],
      todoList: []
    };
  },

  methods: {
    updateTodo(type, val) {
      if (type === "text") this.tempTodo.username = val;
      if (type === "radio") this.tempTodo.partnername = val;
      if (type === "select") this.tempTodo.list = Array.from(val).map(v => v._value);
      this.$emit("update:todo", this.tempTodo);
    }

  },

  async created() {
    const res = await Promise.allSettled([fetch("https://mocki.io/v1/cb950daa-b063-4e8c-bc56-e1d0a900c2bb").then(res => res.json()), fetch("https://mocki.io/v1/d4867d8b-b5d5-4a48-a4ab-79131b5809b8").then(res => res.json())]);
    this.todoList = res[0].value;
    this.partnerList = res[1].value;
  }

};