export default {
  name: "TodoView",

  data() {
    return {
      todo: {
        username: "",
        partnername: "",
        list: []
      },
      step: 0
    };
  },

  computed: {
    showNextStep() {
      let state = true;
      if (this.step === 0) this.todo.username ? state = true : state = false;
      if (this.step === 1) this.todo.partnername ? state = true : state = false;
      if (this.step === 2) state = false;
      return state;
    }

  },
  methods: {
    changeStep(status) {
      status ? this.step++ : this.step--;
    },

    saveTodo() {
      const vm = this;
      let myTodo = JSON.parse(localStorage.getItem("myTodo")) || [];
      myTodo.push({ ...this.todo,
        id: new Date()
      });
      localStorage.setItem("myTodo", JSON.stringify(myTodo));
      this.todo = {
        username: "",
        partnername: "",
        list: []
      };
      setTimeout(() => vm.$router.push({
        path: "/record-view"
      }), 1000);
    }

  }
};